@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;

}

body{
    font: 400 14px Roboto, sans-serif;
    background: #fff;
    -webkit-font-smoothig: antialiased;
}

input, button, textarea{
    font: 400 14px Roboto, sans-serif;
}
button{
    cursor: pointer;
}

form input{
    width: 100%;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 24px;

}
form textarea{
    width: 100%;
    min-height: 140px;
    resize: vertical;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;

}
.button{
    width: 20%;
    height: 30px;
    background: #2E64FE;
    border: 0;
    border-radius: 8px;
    color:#FFF;
    font-weight: 700;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 5px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 30px;
    transition: -webkit-filter 0.8%;
    transition: filter 0.8%;
    transition: filter 0.8%, -webkit-filter 0.8%;
    float: right;

}
.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}
.buttonHome{
    width: 8%;
    height: 30px;
    background: green;
    border: 0;
    border-radius: 8px;
    color:#FFF;
    font-weight: 700;
    margin-top: 5px;
    margin-bottom: 5px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 30px;
    transition: -webkit-filter 0.8%;
    transition: filter 0.8%;
    transition: filter 0.8%, -webkit-filter 0.8%;
    float: right; 
}
.buttonHome:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}
.P_Header{
    display: grid;
    grid-template-columns: 1fr 10fr 2fr;
    grid-gap: 10px;
}
.P_Header span{
    font: 400 2em Roboto, sans-serif;
    background: #fff;
    -webkit-font-smoothig: antialiased; 
    height: 100px; 
    padding-top: 40px;
}
.logon-container{
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}

.logon-container section.form{
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
}

.logon-container section.form form{
    margin-top: 100px;
}
.logon-container section.form form h1{
    font-size: 32px;
    margin-bottom: 32px;
}

.logon-container section.form form a{
    display: flex;
    align-items: center;
    margin-top: 40px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}
.logon-container section.form form a:hover{
    opacity: 0.8;
}
.logon-container section.form form a svg{
    margin-right: 8px;
}
.listSector{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    grid-gap: 0px;
    border-left: 2px solid rgb(214, 108, 9);
    height: 30px;
}
.listHead{
    /*background-color: rgb(100, 121, 200,0.5);*/
    height: 30px;
    padding: 5px 0px 0px 2px;
    vertical-align: center;
    color: rgb(0,0,0, 0.5);
    font-size: 18px;
    
}
.listItem{
    background-color: rgb(123,104,238, 0.2);
    border: 5px solid rgb(255, 255, 255);
    height: 40px;
    padding: 2px 0px 0px 2px;
    vertical-align: center;
    color: rgb(0,0,0, 0.8);
    font-size: 16px;
    
}
.TabTitle{
    color:rgb(255, 87, 51 );
    font-weight: 700;
    margin-top: 10px;
    margin-bottom: 5px;
    display: inline-block;
    text-align: center;
    -webkit-text-decoration: aquamarine;
