   float: left;
   width: 50%;
   margin-left: 25%;
   margin-top: 5%;
   line-height: 30px;
   padding: 1em;
   border: 2px solid #41414d;
   border-radius: 10px;
}
.groupUser input{
    width: 100%;
    height: 30px;
    padding: 2px;
    border: 1px solid #ccc;
    border-radius: 3px;
}
.groupUser button{
    width: 30%;
    height: 30px;
    margin: 1em;
    background-color: dodgerblue;
    color: #fff;
    border: 1px solid #ccc;
}
.groupUser button:hover{
    background-color: royalblue;
}
.groupUser span{
    color:rgb(255, 87, 51 );
    font-weight: 700;
    margin-top: 10px;
    margin-bottom: 5px;
    display: inline-block;
    text-align: center;
    -webkit-text-decoration: aquamarine;
            text-decoration: aquamarine;
    font-size: 20px; 
}
.error-msg{
    color:rgb(255, 0, 0 );
    font-weight: 400;
    -webkit-text-decoration: aquamarine;
            text-decoration: aquamarine;
    font-size: 12px;  
}
